.Menu_DD {
    display:flex;
    flex-direction: column;
    width: 100px;
}

.Menu_Display {
    font-family: "Nanum Brush Script";
    font-size:20px;
    margin: 0 auto;
    width: 100px;
    background-color: rgb(75, 226, 226);
    border: 2px solid rgb(75, 226, 226);
    margin: 0 auto;
    position: relative;
    top: 40px;
}

.Menu_Item {
    font-family: "Nanum Brush Script";
    font-size:20px;
    background-color: rgb(75, 226, 226);
    border: 2px solid rgb(75, 226, 226);
    position: relative;
    top: 40px;
}

.Menu_Item:hover {
    background-color: rgb(61, 202, 202);
}

.Menu_Display:hover {
    background-color: rgb(61, 202, 202);
}