.Title_true {
    position:relative;
    top:30px;
    color: white;
    text-align: center;
    animation: fadeIn 1s;
    -webkit-animation: fadeIn 1s;
    -moz-animation: fadeIn 1s;
    -o-animation: fadeIn 1s;
    -ms-animation: fadeIn 1s;
    font-family: 'Nanum Brush Script';
}

.Title_false {
    position:relative;
    top:30px;
    font-size: 60px;
    color: white;
    text-align: center;
    animation: fadeIn 1s;
    -webkit-animation: fadeIn 1s;
    -moz-animation: fadeIn 1s;
    -o-animation: fadeIn 1s;
    -ms-animation: fadeIn 1s;
    font-family: 'Nanum Brush Script';
}

.List_true {
    position:relative;
    top:30px;
    font-size: 30px;
    color: white;
    animation: fadeIn 1s;
    -webkit-animation: fadeIn 1s;
    -moz-animation: fadeIn 1s;
    -o-animation: fadeIn 1s;
    -ms-animation: fadeIn 1s;
    font-family: 'Nanum Brush Script';
    list-style-type: none;
}

.List_false {
    position:relative;
    top:0px;
    text-align: center;
    font-size: 50px;
    color: white;
    animation: fadeIn 1s;
    -webkit-animation: fadeIn 1s;
    -moz-animation: fadeIn 1s;
    -o-animation: fadeIn 1s;
    -ms-animation: fadeIn 1s;
    font-family: 'Nanum Brush Script';
    list-style-type: none;
}

.Chores_Content_Wrapper {
    background-color: rgb(88, 190, 204);
    width:90%;
    height:90%;
    margin: 0 auto;
    border: 2px solid rg(207, 106, 106);
    border-radius:8px;
    animation: fadeIn 1s;
    -webkit-animation: fadeIn 1s;
    -moz-animation: fadeIn 1s;
    -o-animation: fadeIn 1s;
    -ms-animation: fadeIn 1s;
}


@keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-moz-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-webkit-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-o-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-ms-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }