.MenuComponent {
    display: inline;
    text-align: center;
    position:relative;
    padding-left: 20px;
    padding-right: 20px;
    }

  .MenuText_true {
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: rgb(223, 223, 223);
    font-size:30px;
    text-decoration: none;
    background: none;
    border: none;
    padding-bottom: 5px;
    border-bottom: 2px solid rgba(255, 45, 45, 0.651);
  }

  .MenuText_false {
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: rgb(223, 223, 223);
    font-size:30px;
    text-decoration: none;
    background: none;
    border: none;
  }
  
  .MenuText_false:hover {
      color: rgba(255, 45, 45, 0.651);
      cursor: pointer;
  }

  .MenuText_true:hover {
    color: rgba(255, 45, 45, 0.651);
    cursor: pointer;
}