.ListItem_false {
    color: white;
    font-family: 'Nanum Brush Script';
    font-size: 30px;
    padding-left:15px;
    padding-right:15px;
    text-decoration: none ;
    width:max-content;
}

.ListItem_false:hover{
   cursor:pointer;
   color:rgb(209, 209, 209);
}

.ListItem_true {
    color: white;
    font-family: 'Nanum Brush Script';
    padding-left:15px;
    padding-right:15px;
    font-size: 30px;
    text-decoration: line-through;
    width:max-content;
}

.ListItem_true:hover{
    cursor:pointer;
    color:rgb(209, 209, 209);
}