.About_Content_Wrapper {
    margin: 0 auto;
    border: 2px solid gray;
    border-radius: 10px;
    width: 80%;
    position: relative;
    top: 100px;
    padding: 100px;
    background-color:gray;
    animation: fadeIn 1s;
    -webkit-animation: fadeIn 1s;
    -moz-animation: fadeIn 1s;
    -o-animation: fadeIn 1s;
    -ms-animation: fadeIn 1s;
}

.Title_About {
    color: white;
}

p {
    color: white;
}

.Signature {
    font-family: Nanum Brush Script;
    font-size: 30px;
    position: absolute;
    right: 100px;
}

@keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-moz-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-webkit-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-o-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-ms-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }