.Home_Wrapper {
    width: 80%;
    display:flex;
    flex-direction:row;
    height: 850px;
    margin: 0 auto;
    animation: fadeIn 1s;
    -webkit-animation: fadeIn 1s;
    -moz-animation: fadeIn 1s;
    -o-animation: fadeIn 1s;
    -ms-animation: fadeIn 1s;
}

.Shopping_Wrapper {
    background-color: rgb(207, 106, 106);
    width: 30%;
    margin-right:10px;
    margin-top: 30px;
    margin-left: 50px;
    height:800px;
    border-radius:8px;
}

.HouseTasks_Wrapper {
    background-color: rgb(106, 207, 128);
    width: 30%;
    margin-right:10px;
    margin-top: 30px;
    margin-left: 10px;
    height:800px;
    border-radius:8px;
}

.Chores_Wrapper {
    background-color: rgb(88, 190, 204);
    width: 30%;
    margin-right:10px;
    margin-top: 30px;
    margin-left: 10px;
    height:800px;
    border-radius:8px;
}

@keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-moz-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-webkit-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-o-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-ms-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }