input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

.UIWrapper {
    width:100%;
    margin:0 auto; 
    position:absolute;
    bottom:-3.8vw;
    display: flex;
    flex-direction: grid;
}

.ShoppingNav {
    font-size:21px;
    width:50%;
    border: 2px solid rgb(230, 116, 116);
    background-color:rgb(230, 116, 116);
    color: rgb(223, 223, 223);
    border-radius: 2px;
    font-family: 'Nanum Brush Script';
}


.UIButton {
    font-size:21px;
    width:50%;
    border: 2px solid rgb(230, 116, 116);
    background-color:rgb(230, 116, 116);
    color: rgb(223, 223, 223);
    border-radius: 2px;
    font-family: 'Nanum Brush Script';
}

.UIInput {
    font-family: 'Nanum Brush Script';
    font-size:21px;
    width:50%;
    border: 2px solid rgb(230, 116, 116);
    background-color:rgb(230, 116, 116);
    color: rgb(223, 223, 223);
    border-radius: 2px;
    position:relative;
}   

.UIButton:hover{
    background-color:rgb(228, 89, 89);
    border: 2px solid rgb(228, 89, 89);
    cursor: pointer;
}

.UIInput:hover{
    background-color:rgb(228, 89, 89);
    border: 2px solid rgb(228, 89, 89);
    cursor: pointer;
}

.ShoppingNav:hover{
    background-color:rgb(228, 89, 89);
    border: 2px solid rgb(228, 89, 89);
    cursor: pointer;
}




::placeholder{
    color: rgb(223, 223, 223);
}