
.Shopping_Content_Wrapper {
    background-color: rgb(207, 106, 106);
    width:90%;
    height:90%;
    margin: 0 auto;
    border: 2px solid rg(207, 106, 106);
    border-radius:8px;
    animation: fadeIn 1s;
    -webkit-animation: fadeIn 1s;
    -moz-animation: fadeIn 1s;
    -o-animation: fadeIn 1s;
    -ms-animation: fadeIn 1s;
    position: relative;
}

.Title {
    position:relative;
    top:30px;
    color: white;
    text-align: center;
    animation: fadeIn 1s;
    -webkit-animation: fadeIn 1s;
    -moz-animation: fadeIn 1s;
    -o-animation: fadeIn 1s;
    -ms-animation: fadeIn 1s;
    font-family: 'Nanum Brush Script';
}

.Content_bottom{
    position: absolute;
    bottom: 40px;
    display:flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin: 0 auto;
    width:100%;
    animation: fadeIn 1s;
    -webkit-animation: fadeIn 1s;
    -moz-animation: fadeIn 1s;
    -o-animation: fadeIn 1s;
    -ms-animation: fadeIn 1s;
    height:600px;
}

@keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-moz-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-webkit-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-o-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-ms-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }