.Title {
    color: white;
    font-family: 'Nanum Brush Script';
}

.House_Tasks_Wrapper {
    background-color: rgb(106, 207, 128);
    width:90%;
    height:90%;
    margin: 0 auto;
    border: 2px solid rg(207, 106, 106);
    border-radius:8px;
    animation: fadeIn 1s;
    -webkit-animation: fadeIn 1s;
    -moz-animation: fadeIn 1s;
    -o-animation: fadeIn 1s;
    -ms-animation: fadeIn 1s;
    position: relative;
}

.Underlay {
  background-color: black;
  opacity: 0.2;
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 2;
}

.AddTask_Wrapper {
  background-color: rgb(106, 207, 128);
  width: 45%;
  height: 45%;
  border: 4px solid rgb(106, 207, 128);
  border-radius: 10px;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 29%;
  opacity: 1;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  z-index: 3;
  display: flex;
  flex-direction: column;
}

.Add_Task_Titles {
  color: white;
  font-family: 'Nanum Brush Script';
  font-size: 24px;
  text-align: center;
}

.Desc_input {
  height: 40px;
  width: 80%;
  align-self: center;
}

.Title_input {
  align-self: center;
  width: 40%;
}

.Resp_input {
  align-self: center;
  width: 40%;
  margin: 10px;
}

.CloseMenu_button {
  position: absolute;
  color:rgb(10, 182, 47);
  border: 4px solid rgb(163, 219, 175);
  border-radius: 20px;
  background-color: rgb(163, 219, 175);
  right: 0px;
  top: 0px;
}

.Add_Task_button {
  width: 30%;
  margin: 0 auto;
}

.AddTask_Button {
  font-size:21px;
  width:50%;
  border: 2px solid rgb(149, 219, 146);
  background-color:rgb(149, 219, 146);
  color: rgb(255, 255, 255);
  border-radius: 2px;
  font-family: 'Nanum Brush Script';
}

.FinishedTasks_Button {
  font-size:21px;
  width:50%;
  border: 2px solid rgb(149, 219, 146);
  background-color:rgb(149, 219, 146);
  color: rgb(255, 255, 255);
  border-radius: 2px;
  font-family: 'Nanum Brush Script';
  height: 100%;
}

.FinishedTasks_Button:hover {
  background-color: rgb(65, 212, 60);
  border-color: rgb(116, 228, 112)
}

.AddTask_Button:hover {
  background-color: rgb(65, 212, 60);
  border-color: rgb(116, 228, 112)
}

.content_bottom{
  width:100%;
  margin:0 auto; 
  position:absolute;
  bottom:-35px;
  display: flex;
  flex-direction: grid;
  height: 80px;
}

@keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-moz-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-webkit-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-o-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-ms-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }

